import React from 'react'
import GetAppIcon from '@material-ui/icons/GetApp';
import { Button } from '@material-ui/core';

export const Present = () => {
    return (
        <div className='present col-lg-8 text-white mx-2 my-3'>

            <h1>Hola, Bienvenidos y Bienvenidas</h1>
            <p>Mi nombre es Giancarlo Centurión Camacho, tengo veinticuatro años y
                soy egresado de la carrera de ingeniería de Minas. Me forme de manera autodidacta
                en programación y desarrollo web.
            </p>
            <p>
                En este esapacio quiero compartit contigo mis proyectos, puedes clonar los repositorios
                desde GitHub y divertirte con el código.
            </p>

            <Button variant="contained"
                color="primary"
                startIcon={<GetAppIcon />}
            // href=''>
            >
                <a color ='secondary' className='curri' href="/img/texto.txt" download="CENTURION CAMACHO GIANCARLO RAMON">
                    currículum
                </a>
            </Button>

        </div>
    )
}
